<template>
  <VanPopup
    style="
      box-sizing: border-box;
      width: 100%;
      height: 100%;
      background-color: transparent;
      border-radius: 4px;
    "
    round
    @click-overlay="closePop"
    v-model="show"
  >
    <div class="wrap">
      <div class="window">
        <div class="icon-wrapper">
          <svg class="icon" aria-hidden="true" @click="closePop">
            <use xlink:href="#guanbixiao"></use>
          </svg>
        </div>
        <div class="title">
          {{ $t('warehourse.recharge')
          }}<span translate="no"> {{ tokenName }}</span>
        </div>
        <div class="light5">
          <img src="../../../assets/warehourse/light5.png" alt="" />
        </div>
        <div class="input-wrap">
          <div class="name">{{ $t('warehourse.inputCharge') }}</div>
          <van-field v-model="amount" type="digit" class="input" translate="no"/>
        </div>
        <div class="button-group">
          <VanButton color="#4e4c60" class="btn-cancel" @click="closePop">
            {{ $t('common.cancel') }}
          </VanButton>
          <VanButton
            :disabled="!amount"
            color="#f8bb5a"
            :loading="loading"
            class="btn-confirm"
            @click="handleCharge"
          >
          {{ $t('warehourse.recharge') }}
            <!-- {{ $t('warehourse.pay') }} -->
          </VanButton>
        </div>
        <div class="light6">
          <img src="../../../assets/warehourse/light4.png" alt="" />
        </div>
      </div>
    </div>
  </VanPopup>
</template>

<script>
import contractToken from '@/contracts/contractToken'

export default {
  props: ['toAddress', 'tokenAddress', 'tokenName'],
  name: '',
  data () {
    return {
      show: true,
      amount: null,
      loading: false
    }
  },
  methods: {
    closePop () {
      this.$emit('close')
    },

    async handleCharge () {
      this.loading = true
      const resp = await contractToken.transfer(this.toAddress, this.amount, this.tokenAddress)
      this.loading = false
      if (!resp.success) return
      this.$emit('success')
      this.closePop()
    },
    handleConfirm () {
      this.$emit('close')
    }
  },
  mounted () {

  }
}
</script>

