<template>
  <VanPopup
    style="
      box-sizing: border-box;
      width: 100%;
      height: 100%;
      background-color: transparent;
      border-radius: 4px;
    "
    round
    @click-overlay="closePop"
    v-model="show"
  >
    <div class="wrap">
      <div class="window">
        <div class="icon-wrapper">
          <svg class="icon" aria-hidden="true" @click="closePop">
            <use xlink:href="#guanbixiao"></use>
          </svg>
        </div>
        <div class="title">{{ $t('warehourse.withdraw') }}SFO</div>
        <div class="light5">
          <img src="../../../assets/warehourse/light5.png" alt="" />
        </div>
        <div class="balance">
          <div class="key">SFO{{ $t('warehourse.balance') }}：</div>
          <div class="value" translate="no">
            {{ $gbUtils.formatBalance(dataSource.amount) }}
          </div>
        </div>
        <div class="balance">
          <div class="key">{{ $t('warehourse.canBalance') }}：</div>
          <div class="value" translate="no">
            {{ $gbUtils.formatBalance(dataSource.activityAmount) }}
          </div>
        </div>
        <div class="input-wrap">
          <div class="name">{{ $t('warehourse.inputNum') }}</div>
          <van-field v-model="amount" type="digit" class="input" translate="no"/>
        </div>
        <div class="button-group">
          <VanButton
            :disabled="!amount || +amount > +dataSource.activityAmount"
            color="#f8bb5a"
            :loading="loading"
            class="btn-confirm"
            @click="handleWithdraw"
          >
            {{ $t('warehourse.withdraw') }}
          </VanButton>
        </div>
        <div class="notice">{{ $t('warehourse.notice') }}</div>
        <div class="light6">
          <img src="../../../assets/warehourse/light4.png" alt="" />
        </div>
      </div>
    </div>
  </VanPopup>
</template>

<script>
// import web3 from '@/utils/web3'

export default {
  props: ['toAddress', 'dataSource'],
  name: '',
  data () {
    return {
      show: true,
      amount: null,
      loading: false
    }
  },
  methods: {
    closePop () {
      this.$emit('close')
    },
    async handleWithdraw () {
      this.loading = true
      console.log('token', ('0' + this.dataSource.tokenType).slice(-2))
      const amount = `0.0010${('0' + this.dataSource.tokenType).slice(-2)}`
      const resp = await this.$web3.transfer(this.toAddress, amount, this.amount)
      this.loading = false
      if (!resp.success) return
      this.$toast('success')
      this.$parent.reload()
      this.closePop()
    },
    handleConfirm () {
      this.$emit('close')
    },
    strReverse (str1) {
      return str1.split('').reverse().join('')
    }
  },
  mounted () {

  }
}
</script>

