<template>
  <div class="warehourse-wrap">
    <div id="header">
      <div class="name">
        {{ $t('warehourse.totalMoney') }}<span>（$）</span>
      </div>
      <div class="assets" translate="no">
        <VanLoading v-if="balanceLoading"></VanLoading>
        <span v-else>{{ $gbUtils.formatBalance(myBalance) }}</span>
      </div>
    </div>
    <div id="assets-detal">
      <div class="title">
        <div class="name">{{ $t('warehourse.detail') }}</div>
        <div class="tab">
          <div
            class="tab-item"
            :class="{ active: tabSelect === 0 }"
            @click="handleChangeTab(0)"
          >
            {{ $t('warehourse.recharge') }}
          </div>
          <div
            class="tab-item"
            :class="{ active: tabSelect === 1 }"
            @click="handleChangeTab(1)"
          >
            {{ $t('warehourse.withdrawB') }}
          </div>
        </div>
      </div>
      <div
        v-for="(item, index) in miningInfo.supportTokenList"
        :key="item.tokenType"
        class="token-item"
      >
        <div class="left" translate="no">
          <div class="detail">
            <div class="name">{{ item.tokenName }}</div>
            <svg class="icon" aria-hidden="true" @click="handleTokenShow(item)">
              <use xlink:href="#chakan-copy"></use>
            </svg>
          </div>
          <div class="value">
            {{ getBalance(item.tokenName) }}
            <span>≈${{ getU(item.tokenName) }}</span>
          </div>
        </div>
        <div class="button-group">
          <div
            class="button"
            @click="handleRechargeShow(item.tokenAddress, item.tokenName)"
            v-if="tabSelect === 0"
          >
            {{ $t('warehourse.recharge') }}
          </div>
          <VanButton
            color="#7e6e53"
            class="button"
            :loading="withdrawLoading"
            :disabled="getBalance(item.tokenName) <= 0"
            @click="handleExtract(item.tokenType, index)"
            v-else
          >
            {{ $t('warehourse.withdrawB') }}
          </VanButton>
        </div>
      </div>
    </div>
    <!-- 交易记录 -->

    <div id="trade-records">
      <div class="name">{{ $t('warehourse.tx') }}</div>
      <div class="list-title">
        <div class="title-item">{{ $t('warehourse.type') }}</div>
        <div class="title-item">{{ $t('warehourse.num') }}</div>
        <div class="title-item time">{{ $t('warehourse.time') }}</div>
        <div class="title-item">{{ $t('warehourse.souce') }}</div>
      </div>
      <div class="list-value" v-for="item in txList" :key="item.id">
        <div class="value-type item" translate="no">
          {{ item.tokenName || 'SFO' }}
        </div>
        <div class="value-num item">
          {{ $gbUtils.handleRound(item.amount) }}
        </div>
        <div class="time item">{{ item.createTime || '0:0:0' }}</div>
        <div class="value-get item">
          {{ $t(`warehourse.status[${item.operation}]`) }}
        </div>
      </div>
    </div>
    <RechargePop
      v-if="rechargeShow"
      @close="handleRechargeShow"
      @success="handleRechargeSuccess"
      :toAddress="miningInfo.rechargeAddress"
      :tokenAddress="rechargeToken"
      :tokenName="tokenName"
    ></RechargePop>
    <WithdrawPop
      v-if="withdrawShow"
      :toAddress="miningInfo.fromAddress"
      :dataSource="myAssetsList[0]"
      @close="handleWithdrawShow"
    ></WithdrawPop>
    <CommomPop @close="handleTokenShow" v-if="tokenShow" class="token-wrap">
      <div class="title">{{ $t('warehourse.tokenDetail') }}</div>
      <div class="item">
        <div class="key">{{ $t('warehourse.tokenName') }}</div>
        <div class="value">{{ tempToken.tokenName }}</div>
      </div>
      <div class="item">
        <div class="key">{{ $t('warehourse.contract') }}</div>
        <div class="value" v-if="tempToken.tokenName !== 'SFO'">
          {{ $gbUtils.formatAddress(tempToken.tokenAddress, '...', 6) }}
          <svg
            class="icon"
            aria-hidden="true"
            @click="handleCopy(tempToken.tokenAddress)"
          >
            <use xlink:href="#fuzhi"></use>
          </svg>
        </div>
        <div class="value" v-else>{{ $t('common.notOpen') }}</div>
      </div>
      <div class="button" @click="handleTokenShow">
        {{ $t('common.confirm') }}
      </div>
    </CommomPop>
  </div>
</template>

<script>
import routerObj from '@/contracts/contractRouter'
import { handleMining, handleWarehourseList } from '@/services/warehourse'
import RechargePop from './components/RechargePop.vue'
import WithdrawPop from './components/WithdrawPop.vue'
import CommomPop from '../../components/common/CommomPop.vue'
export default {
  name: '',
  data () {
    return {
      tabSelect: 0, // 0收取 1提币
      rechargeShow: false,
      withdrawShow: false,
      tokenShow: false,
      tokenName: '',
      miningInfo: {},
      myBalance: 0,
      rechargeToken: '',
      myAssetsList: [],
      txList: [],
      balanceLoading: true,
      withdrawLoading: false,
      tempToken: {}
    }
  },
  inject: ['reload'],
  computed: {
  },
  methods: {
    getBalance (tokenName) {
      const index = this.myAssetsList.findIndex(
        (token) => {
          return token.name === tokenName
        }
      )
      if (index > -1) {
        return this.$gbUtils.formatBalance(this.myAssetsList[index].amount)
      } else {
        return 0
      }
    },
    getU (tokenName) {
      const index = this.myAssetsList.findIndex(
        (token) => {
          return token.name === tokenName
        }
      )
      if (index > -1) {
        return this.$gbUtils.formatBalance(this.myAssetsList[index].amount * (this.myAssetsList[index].price || (+this.miningInfo.assetsList[index].tokenType === 1 ? 0.15 : 0)))
      } else {
        return 0
      }
    },
    handleChangeTab (index) {
      this.tabSelect = index
    },
    handleRechargeShow (tokenAddress, tokenName) {
      this.rechargeToken = tokenAddress
      this.tokenName = tokenName
      this.rechargeShow = !this.rechargeShow
    },
    handleWithdrawShow () {
      this.withdrawShow = !this.withdrawShow
    },
    handleCopy (content) {
      const text = document.createElement('textarea')
      text.value = content
      document.body.appendChild(text)
      text.select() // 选择对象
      if (document.execCommand('copy')) {
        document.execCommand('copy')
        this.$toast('copied')
      }
      // 执行浏览器复制命令
      document.body.removeChild(text)
    },
    // 获取header数据
    async getMining () {
      this.balanceLoading = true
      this.myBalance = 0
      const resp = await handleMining()
      if (!resp.success) return
      this.miningInfo = resp.data
      this.myAssetsList = resp.data.assetsList
      this.miningInfo.supportTokenList.forEach(item => {
        item.loading = false
      })
      this.handleTotalAmount()
      this.balanceLoading = false
    },
    // 计算总u
    async handleTotalAmount () {
      this.myBalance = 0
      for (let i = 0; i < this.miningInfo.assetsList.length; i++) {
        const resp = await routerObj.getAmountsIn(
          this.miningInfo.assetsList[i].token
        )
        console.log(resp)
        // if (!resp.success) return
        this.miningInfo.assetsList[i].price = resp.result
        this.myBalance += (+resp.result || (+this.miningInfo.assetsList[i].tokenType === 1 ? 0.15 : 0)) * +this.miningInfo.assetsList[i].amount
      }
    },
    // 列表
    async getList () {
      const resp = await handleWarehourseList({
        orderByColumn: 'id',
        isAsc: 'desc'
      })
      if (!resp.success) return
      this.txList = resp.rows
    },
    // 提取收益  不填id提取全部
    async handleExtract (tokentype, index) {
      if (index === 0) {
        return this.$toast(this.$t('common.notOpen'))
        // this.withdrawShow = true  开放时开启
      } else {
        this.withdrawLoading = true
        this.handleWithdraw(index)
      }
    },
    async handleWithdraw (index) {
      this.miningInfo.supportTokenList[index].loading = true
      console.log('this.miningInfo.supportTokenList', this.miningInfo.supportTokenList)
      console.log(this.miningInfo.supportTokenList[index])
      const amount = `0.0010${('0' + this.miningInfo.supportTokenList[index].tokenType).slice(-2)}`
      const resp = await this.$web3.transfer(this.miningInfo.fromAddress, amount, this.amount)
      this.withdrawLoading = false
      this.miningInfo.supportTokenList[index].loading = false
      if (!resp.success) return
      this.reload()
      this.$toast('sucess')
    },
    handleTokenShow (temp) {
      this.tempToken = temp
      console.log('temp', temp)
      this.tokenShow = !this.tokenShow
    },
    handleRechargeSuccess () {
      console.log('success')
      this.reload()
    }
  },
  mounted () {
    this.getMining()
    this.getList()
  },
  components: { RechargePop, WithdrawPop, CommomPop }
}
</script>

